body {
  font-family: Cairo !important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.iconButton {
  transition: 0.3s !important;
}
.iconButton:hover {
  background-color: #c5c5c5 !important;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);
}
.todoCard {
  transition: 0.3s !important;
}
.todoCard:hover {
  /* #283593 */
  padding-bottom: 20px !important;
  padding-top: 20px !important;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);
}

/* start custom fonts */
@font-face {
  font-family: "Cairo", sans-serif;
  src: url(../public/fonts/Cairo-Regular.ttf);
  font-weight: normal;
}
@font-face {
  font-family: "Cairo", sans-serif;
  src: url(../public/fonts/Cairo-Bold.ttf);
  font-weight: bold;
}
@font-face {
  font-family: "Cairo", sans-serif;
  src: url(../public/fonts/Cairo-Black.ttf);
  font-weight: 900;
}
@font-face {
  font-family: "CairoThin", sans-serif;
  src: url(../public/fonts/Cairo-ExtraLight.ttf);
  font-weight: 100;
}
@font-face {
  font-family: "CairoThin", sans-serif;
  src: url(../public/fonts/Cairo-Light.ttf);
  font-weight: 200;
}

/* end custom fonts */
